.card {
    background: var(--slate-transparent);
    display: inline-flex;
    width: 625px;
    
    min-height: 375px;
    margin: 1.25%;
    font-size: 1em;
    flex-direction: column;
    justify-content: center;
    
    border-radius: 25px;
}

.header {
    height: 50%;
    display: flex;
    flex-direction: row;
}

.header img {
    margin: 5%;
}

.header div.info {
    display: flex;
    margin: 5%;
    width: 100%;
    justify-content: center;
    flex-direction: column;

}

.footer {
    height: auto;
    margin: 2.5%;
    display: flex;
    flex-direction: row;
    justify-content: left;
    overflow-x: auto
}

.rewardDiv {
    min-height: 70px;
    margin: 2.5%;
    margin-top: 0;
    background-color: var(--slate);
    height: auto;
    width: 100px;
    overflow: hidden;
    padding: 2.5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    border-color: var(--text);
    border-radius: 10%;
    border-width: 15%;
    border-style: solid;
    
    text-shadow: 0 0 .5px var(--text);
}

.rewardDiv * {
    margin: 0;

}

.rewardDiv h2 {
    margin-bottom: 15px;
}

.rewardDiv h5 {
    width: 100%;
    word-wrap: normal;
}

.card img {
    max-width: 40%;
    height: auto;
}

@media (max-width: 900px) {
    .card {
        width: 97.5%;
        min-height: 275px;
    }
    .card h2 {
        font-size: 100%;
    }
    .card h3 {
        font-size: 85%;
    }
    .card h5 {
        font-size: 70%;
    }
    .card .cardButton {
        font-size: 60%;
    }
}