.table {
    display: table;
    width: 100%;
}

.tr {
    width: 100%;
    display: table-row;
}
.thead {
    display: table-header-group;
}
.tbody {
    display: table-row-group
}
.tfoot {

    display: table-footer-group
}

.td, .th {
    margin: auto;
    display: table-cell;
}

.th,.footButton {
    font-weight: bold;
}

div.tr span[col="balance"]::before,div.tr span.th[col="balance"]::before,div.tr span[col="amount"]::before,div.tr span.th[col="amount"]::before {
    content:'$'
}

.formInput {
    width: 100%;
}

