#accountingContainer {
    height: 100%;
    width: 100%;
    margin: 1.25% 1.25% 0;
    display: grid;
    grid-template-columns: repeat(3,calc(91%/3));
    grid-template-rows: repeat(2,calc(91%/2));
    gap: 3%;
}

div.accountingCard {
    
    border-radius: 25px;
    overflow: hidden;
    background-color: var(--slate-transparent)
}

div.interior {
    padding: 5%;
}

#accountingSummary {
    grid-row: 1 / span 3
}

#accountingTable {
    grid-column: 2 / span 1;
    grid-row: 1 / span 2
}

#accountingGraph {
    padding: 2.5%;
    grid-column: 2 / span 2;
}

.stAccounting {
    grid-column: 1 / span 1;
    grid-row: 1 / span 1
}

.ltAccounting {
    grid-column: 1 / span 1;
    grid-row: 2 / span 1
}

.accountingGraph {
    grid-column: 2 / span 2;
    grid-row: 2 / span 1;
}

.allAccounting {
    grid-column: 2 / span 2;
    grid-row: 1 / span 1;
}

.nameDiv input {
    width: 45%;
}
.formButtons {
    width: 100%;
}
.formButtons button {
    width: 33%;
    padding: 1%;
}
.tableDiv {
    display: table;
    width: 100%;
}

.tr {
    width: 100%;
    display: table-row;
}
.tableHeader {
    display: table-header-group;
}
.tableBody {
    display: table-row-group
}
.tableFooter {

    display: table-footer-group
}

.td, .th {
    margin: auto;
    display: table-cell;
}

.th {
    font-weight: bold;
}

div.tr span[col="balance"]::before,div.tr span.th[col="balance"]::before {
    content:'$'
}


@media (max-width:768px) {
    .accountingGraph, .allAccounting {
        display: none;
    }
    .stAccounting {
        overflow: auto;
        grid-column: 1 / span 3;
    }
    .ltAccounting {
        overflow: auto;
        grid-column: 1 / span 3;
    }
    
    
}