#dash {
    position:  relative;
    height: 100%;
}


#dash div#container {
    height: 100%;
    width: 97.5%;
}

#dash div#container div h2{
    margin: auto 0;
    
    height: auto;
}
#dash div#container div p {
    margin: auto 0;
    
}

#dash div#container div.content {
    height: 100%;
    margin-bottom: 7.5%;
    overflow: hidden;
    
    display: grid;
    grid-template-columns: repeat(3,30%);
    grid-template-rows: repeat(2,47.5%);
    gap: 3%;
}

#dash div#container div.header {
    padding: 0 1.25%;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
div.header * {
    overflow: hidden;
}

@media (max-width: 768px) {
    #dash div#container div.content {
        height: 97.5%;
        grid-template-columns: repeat(2,47.5%);
    }
}