div.AddWidget {
    width: 100%;
    height: 100%;
    position: absolute;

    z-index: 1;
    background-color: var(--background-color-transparent);
    text-align: center;
    display: flex;
    justify-content:center;
}

div.AddWidget div.container {
    width: 50%;
    height: 50%;
    margin: auto;
    opacity: 1;
    background-color: var(--slate);
    border-radius: 25px;
}