#main {
    display: flex;
    flex-direction: row;
    height: 100%;
}



.content {
    width: calc(100% - var(--menu-width));
    height: auto;
    padding: 2.5%;
    display: inline-block;
    margin-left: var(--menu-width);
    text-align: left;
    background-color: var(--background-color);
    
}



@media (max-width:900px) {
    .content {
        width: 100%; 
        margin-left: 0;
        margin-top: calc(var(--menu-mobile-height) + 2.5%)
    }
    
}




