
div#editBudgetContainer {
    background: var(--slate);
    margin: auto;
    width: 50%;
    height: 75%;
    border-radius: 25px;
    padding: 1%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

div#editBudget {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    margin-top: 0 !important;
    z-index: 1;
    background-color: var(--background-color-transparent);
    text-align: center;
    display: flex;
    justify-content:center;
}

div#BudgetHeader {
    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}