#incomeContainer {
    height: 100%;
    width: 100%;
    margin: 1.25% 1.25% 0;
    display: grid;
    grid-template-columns: repeat(3,calc(91%/3));
    grid-template-rows: repeat(2,45%);
    gap: 3%;
    
    
}

.container {
    padding: 5%;
}

#incomeGraph {
    height: 100%;
    
    
    grid-column: 1 / span 3;
}

div.incomeCard {
    width: 100%;
    
    border-radius: 25px;
    overflow: hidden;
    margin: 0;
    
    background-color: var(--slate-transparent)
}

div.incomeGraphContainer {
    width: auto;
    height: 100%;
    border-radius: 25px;
    overflow: hidden;
    
    padding: 1.25%;
    background-color: var(--slate-transparent)
}

tr.income {
    line-height: 1.5em;
}
tr.income td {
    padding: 0 1%;
}

@media (max-width:768px) {
    #incomeContainer {
        grid-template-columns: 97.5%;
        grid-template-rows: repeat(2,47.5%);
        gap: 2.5%;
    }

    .incomeGraphContainer, #ytdIncome  {
        display: none;
    }
    #actualIncome{
        grid-row: 2 / span 1;
        overflow: auto;
    }
    
    #expectedIncome {
        grid-row: 1 / span 1;
        overflow: auto;
    }

    

}

