#menu {
    background: var(--menu-color);
    width: var(--menu-width);
    
    height: 100vh;
    position:fixed;
}

#menu nav ul li {
    list-style: none;
    text-align: left;
    padding: 10px 0px;
    height: 2.5em;
    
}

nav ul li a {
    text-decoration: none;
    color: var(--light-text);
    font-size: 1.5rem;
}

nav ul li a::before {
    display: none;
}




#profile {
    
    margin: 10% auto;
    width: 50%;
    height: auto;
    
    background: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    overflow: hidden;
}

#profile img {
    border-radius: 50%;
    cursor: pointer;
}

.activeLink {
    padding-left: 7.5px;
    
}

li:has(.activeLink) {
    
    border-right: 7.5px var(--light-text) solid;
}
button.navButton {
    color: var(--light-text);
    font-size: 1.5rem;
    height: 2rem;
    padding: 0;

}

button.navButton:hover {
    color: var(--slate)

}

@media (max-width: 900px) {
    #menu {
        height: var(--menu-mobile-height);
        width: 100%;
        
    }
    nav {
        margin: auto;
        width: 75%;
        height: 100%;
    }
    
    #menu nav ul li a {
        font-size: .75rem;
        
    }

    #menu nav ul li a::before {
        display: initial
    }

    #menu nav ul li{
        
        
        width: fit-content;
    }

    #menu nav ul li {
        
        height: calc(45%);
        padding-left: 5px;
        padding-right: 5px;
        
        
    }
    #menu nav ul li a {

    }


    .activeLink {
        padding: 0;
    }
    li:has(.activeLink) {
        border-right: none;
        border-bottom: 7.5px var(--light-text) solid;
    }

    #menu nav ul{
        padding-left: 0;
        margin: auto;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }


    #profile {
        all: unset;
        display: none;
    }

    button.navButton {
        color: var(--light-text);
        font-size: .75rem;
        height: auto;
        padding: 0;
    
    }

}

