#budgetContainer {
    height: 100%;
    width: 100%;
    margin: 1.25% 1.25% 0;
    display: grid;
    grid-template-columns: repeat(3,calc(91%/3));
    grid-template-rows: repeat(3,calc(91%/3));
    gap: 3%;
}

div.budgetCard {
    
    border-radius: 25px;
    overflow: hidden;
    background-color: var(--slate-transparent)
}

div.interior {
    padding: 5%;
}

#budgetSummary {
    grid-row: 1 / span 3
}

#budgetTable {
    grid-column: 2 / span 1;
    grid-row: 1 / span 2
}

#budgetGraph {
    padding: 2.5%;
    grid-column: 2 / span 2;
}

@media (max-width:768px) {
    #budgetContainer {
        grid-template-columns: 97.5%;
        grid-template-rows: 97.5%;
    }
    #budgetGraph,#budgetSummary,#savingsSummary {
        display: none;
    }
    #budgetTable {
        grid-column: 1 / span 1;
        grid-row: 1 / span 1;
    }
}