
#expenseContainer {
    height: 100%;
    width: 100%;
    margin: 1.25% 1.25% 0;
    display: grid;
    grid-template-columns: repeat(3,calc(91%/3));
    grid-template-rows: repeat(3,calc(91%/3));
    gap: 3%;
}



 div.expenseCard {
    
    border-radius: 25px;
    overflow: hidden;
    background-color: var(--slate-transparent)
}



div.scrollableOverflow {
    overflow: auto;
    text-align: left;

}

div.allExpenses {
    grid-column: 2 / span 2;
    grid-row: 1 / span 3;
    width: 100%;
    
}

.interior {
    padding: 5%;
}

div.dashExpenses {
    width: 100%;
    grid-row: 1 / span 2;
}

div.extraDash { 
    grid-row: 3 / 3;
    width: 100%;
    overflow: hidden !important;
}

table {
    width: 100%;
}

tfoot {
    font-weight: bold;
}
tfoot button {
    font-weight: bold;
}

tr {
    margin: 0;
    padding: 0;
}

td {
    padding: 0;
}

td[col='amount']::before {
    content: '$';
}

td[col='index'] {
    max-width: 35px;
}

tr.expenses td:not([col='index']) {
    
    padding: 0 1%;
    font-size: .9em;
    margin: auto;
}

td ~ td {
    border-left: 1px var(--background-color-transparent) solid
}



@media (max-width: 768px) {
    div.allExpenses {
        width: 100%;
        grid-column: 1 / span 3;
    }

    div.dashExpenses,.extraDash {
        display: none;
    }

}